<template>
  <v-container>
    <v-row class="admin-schedule">
      <div class='calendar'>
        <FullCalender :options="calenderOptions" />
      </div>
    </v-row>

    <!-- 休診切り替えダイアログ -->
    <v-dialog v-model="showSwitchBizDialog" max-width="300">
      <v-card prepend-icon="mdi-home-switch-outline" title="営業/休診 切替">
        <v-card-text>
          <p>{{switchDate}}を休診日に切り替えますか？</p>
        </v-card-text>
        <v-card-actions>
          <v-btn color="primary" @click="showSwitchBizDialog = false">キャンセル</v-btn>
          <v-spacer></v-spacer>
          <v-btn
            class="exec-button"
            text="切替"
            @click="switchBiz"
          ></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- 予約作成ダイアログ -->
    <v-dialog v-model="showMakeReservationDialog" max-width="500">
      <v-card title="予約作成">
        <v-card-text>
          <v-table fixed-header density="compact">
            <tbody>
              <tr>
                <td class="text-right">日時 :</td>
                <td class="text-left">{{ showReservationTime() }}</td>
              </tr>
              <tr>
                <td class="text-right">予約作成者 :</td>
                <td class="text-left">{{ reserveInfo.creater }}</td>
              </tr>
              <tr>
                <td class="text-right">備考 :</td>
                <td class="text-left">
                  <v-textarea
                    class="pt-2"
                    label="備考があれば入力してください"
                    density="compact"
                    v-model="reserveInfo.note"
                    maxlength="100"
                    rows="2"
                  ></v-textarea>
                </td>
              </tr>
            </tbody>
          </v-table>

        </v-card-text>
        <v-card-actions>
          <v-btn variant="plain" @click="showMakeReservationDialog = false">閉じる</v-btn>
          <v-btn
            class="exec-button"
            text="予約登録"
            append-icon="mdi-book-plus-outline"
            @click="makeReservation"
          ></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- 予定確認ダイアログ -->
    <v-dialog v-model="showReservationInfoDialog" max-width="600">
      <v-card>
        <v-card-title>予約情報</v-card-title>
        <v-card-text>
          <v-table fixed-header density="compact">
            <tbody>
              <tr>
                <td class="text-right">日時 :</td>
                <td class="text-left">{{ reserveInfo.date }}</td>
              </tr>
              <tr>
                <td class="text-right">予約者ID :</td>
                <td class="text-left">{{ reserveInfo.id }}</td>
              </tr>
              <tr>
                <td class="text-right">予約者氏名 :</td>
                <td class="text-left">{{ reserveInfo.name }}</td>
              </tr>
              <tr>
                <td class="text-right">氏名(カナ) :</td>
                <td class="text-left">{{ reserveInfo.name_kana }}</td>
              </tr>
              <tr>
                <td class="text-right">メールアドレス :</td>
                <td class="text-left">{{ reserveInfo.mail }}</td>
              </tr>
              <tr>
                <td class="text-right">備考 :</td>
                <td class="text-left">{{ reserveInfo.memo }}</td>
              </tr>
            </tbody>
          </v-table>
        </v-card-text>
        <v-card-actions>
          <v-btn color="primary" @click="showReservationInfoDialog = false">閉じる</v-btn>
          <v-spacer></v-spacer>
          <v-btn
            class="del-button"
            text="予約を削除する"
            prepend-icon="mdi-trash-can-outline"
            @click="confirmDelete"
          ></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- 削除確認ダイアログ -->
    <v-dialog v-model="showConfirmDeleteDialog" max-width="300">
      <v-card prepend-icon="mdi-delete-outline" title="削除の確認">
        <v-card-text>
          <p>本当に削除してもよろしいですか？</p>
        </v-card-text>
        <v-card-actions>
          <v-btn color="primary" @click="showConfirmDeleteDialog = false">キャンセル</v-btn>
          <v-spacer></v-spacer>
          <v-btn
            class="del-button"
            text="削除する"
            @click="deleteReservation"
          ></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

  </v-container>
</template>

<script>
import jaLocale from '@fullcalendar/core/locales/ja'
import FullCalender from '@fullcalendar/vue3';
import dayGridPlugin from '@fullcalendar/daygrid';
import timeGridPlugin from '@fullcalendar/timegrid'
import interactionPlugin from '@fullcalendar/interaction';
import { resetObject, addMinutesToDate, formatDateString, convertMinutesToHHMMSS, formatHhMmString } from '@/utils';

export default {
  name: 'AdminSchedule',
  components: {
    FullCalender,
  },
  data() {
    return {
      // FullCalendarオプション
      calenderOptions: {
        plugins: [dayGridPlugin, interactionPlugin, timeGridPlugin],
        locale: jaLocale,
        initialView: 'timeGridDay',
        firstDay: 1,  // 月曜始まり
        height: 'auto',
        headerToolbar: {
          left: 'prev today',
          center: 'title',
          right: 'dayGridMonth timeGridWeek timeGridDay next',
        },
        buttonText: {
          today: '本日',
          month: '月',
          week: '週',
          day: '日',
          list: 'リスト'
        },
        contentHeight: 'auto',
        navLinks: true,
        // hiddenDays: [ 0 ],        // 日曜は非表示
        slotDuration: this.getTimeDuration(),
        slotMinTime: '09:00:00',  // 週/日表示用
        slotMaxTime: '20:00:00',  // 週/日表示用
        nowIndicator: true,       // 週/日表示用
        dayMaxEvents: false,      // 週/日表示用
        eventDisplay: 'block',    // 週/日表示用
        eventSources: [
          this.getEvents(),
        ],
        dayCellContent: (arg) => {
          if (arg.dayNumberText !== '') {
            const day = String(arg.date.getDate()).padStart(2, '0');
            return day;
          }
        },
        dateClick: (arg) => {
          if (arg.allDay) {
            const date = formatDateString(arg.date, '-');
            this.switchDate = date;
            this.showSwitchBizDialog = true;
          } else {
            this.reserveInfo.date = arg.date;
            this.reserveInfo.creater = '斎藤先生';
            this.reserveInfo.note = '';
            this.showMakeReservationDialog = true;
          }
        },
        eventClick: (arg) => {
          // DBから予約情報を取得し、展開する。
          resetObject(this.reserveInfo);

          if (arg.event.allDay) {
            const date = formatDateString(arg.event.start, '-');
            this.switchDate = date;
            this.showSwitchBizDialog = true;
            return;
          } else {
            const start = formatDateString(arg.event.start, '-', true);
            const end = formatHhMmString(arg.event.end);
            this.reserveInfo.date = `${start} ～ ${end}`;
          }
          this.reserveInfo.id = `h99999`;
          this.reserveInfo.name = arg.event.title;
          this.reserveInfo.name_kana = `(カタカナ読み)`;
          this.reserveInfo.mail = `xxx@xxx.test`;
          this.reserveInfo.memo = `予約時に入力した特記事項がここに表示される。`;

          this.showReservationInfoDialog = true;
        },
      },
      // 予約時間間隔
      reserveDuration: 15,
      // 営業/休診切り替えダイアログ関連
      showSwitchBizDialog: false,
      switchDate: '',
      // 予約作成ダイアログ関連
      showMakeReservationDialog: false,
      reserveInfo: {
        date: '',
        id: '',
        name: '',
        name_kana: '',
        mail: '',
        creater: '',
        memo: '',
      },
      // 予約情報ダイアログ関連
      showReservationInfoDialog: false,
      showConfirmDeleteDialog: false,
    }
  },
  created() {
    this.getHolidays();
  },
  methods: {
    /** 予約取得処理 */
    getEvents() {
      // 本来はBackendから予約リストを取得する。
      const now = new Date();
      const today = formatDateString(now, '-');
      const list = [];
      list.push({ title: "山田 太郎", start: `${today}T10:30:00`, end: `${today}T10:45:00` });
      list.push({ title: "佐藤 花子", start: `${today}T14:00:00`, end: `${today}T14:15:00` });
      list.push({ title: "田中 二郎", start: `${today}T15:30:00`, end: `${today}T15:45:00` });

      const nextDate = new Date(now);
      nextDate.setDate(now.getDate() + 1);
      const tomorrow = formatDateString(nextDate, '-');
      list.push({ title: "休診日", start: `${tomorrow}T00:00:00`, allDay: true });

      return list;
    },
    /** 予約時間間隔取得処理 */
    getTimeDuration() {
      // 本来はBackendからTimeSpanを取得する。
      this.reserveDuration = 15;

      const duration = convertMinutesToHHMMSS(this.reserveDuration);
      return duration;
    },
    /** 休日取得処理 */
    getHolidays() {
      const list = [];
      // 本来はBackendから休日リストを取得する。
      list.push("2024-12-19");
      list.push("2024-12-24");

      return list;
    },
    // 予約時間表示処理
    showReservationTime() {
      const beginStr = formatDateString(this.reserveInfo.date, '-', true);
      const end = addMinutesToDate(this.reserveInfo.date, this.reserveDuration);
      const durationStr = `${beginStr} ～ ${formatHhMmString(end)}`;

      return durationStr;
    },
    switchBiz() {
      // TODO : 開業/休診切り替え処理の実装
      this.showSwitchBizDialog = false;
      this.switchDate = '';
    },
    makeReservation() {
      // TODO : 予約実行
      this.showMakeReservationDialog = false;
    },
    confirmDelete() {
      // TODO : 予約削除実行
      this.showConfirmDeleteDialog = true;
    },
    deleteReservation() {
      // TODO : 予約削除実行
      this.showConfirmDeleteDialog = false;
      this.showReservationInfoDialog = false;
    },
  }
};
</script>

