<template>
  <v-container class="reg-user">
    <v-card title="登録するユーザ情報を入力してください">
      <small class="text-caption text-medium-emphasis">*は必須項目です</small>
      <v-card-text>
        <v-row>
          <v-text-field density="compact" required label="氏名*"
            v-model="inputs['name'].input"
            maxlength="30"
            :error="!!inputs['name'].errMsg"
            :error-messages="inputs['name'].errMsg">
          </v-text-field>
        </v-row>
        <v-row>
          <v-text-field density="compact" required label="氏名(カナ)*"
            v-model="inputs['nameKana'].input"
            maxlength="30"
            :error="!!inputs['nameKana'].errMsg"
            :error-messages="inputs['nameKana'].errMsg">
          </v-text-field>
        </v-row>
        <v-row>
          <v-text-field density="compact" required label="メールアドレス*"
            type="email"
            maxlength="255"
            v-model="inputs['mail'].input"
            :error="!!inputs['mail'].errMsg"
            :error-messages="inputs['mail'].errMsg">
          </v-text-field>
        </v-row>
        <v-row>
          <v-text-field density="compact" required label="電話番号"
            v-model="inputs['phone'].input"
            :rules="[phoneNumberRule]"
            :input="validatePhoneNumber"
            maxlength="20"
            :error="!!inputs['phone'].errMsg"
            :error-messages="inputs['phone'].errMsg">
          </v-text-field>
        </v-row>
        <v-row>
          <v-text-field density="compact" required label="パスワード*"
            :append-inner-icon="visiblePass ? 'mdi-eye-off-outline' : 'mdi-eye-outline'"
            :type="visiblePass ? 'text' : 'password'"
            v-model="inputs['pass'].input"
            maxlength="64"
            :error="!!inputs['pass'].errMsg"
            :error-messages="inputs['pass'].errMsg"
            @click:append-inner="visiblePass = !visiblePass"
          >
          </v-text-field>
        </v-row>
        <v-row>
          <v-text-field density="compact" required label="パスワード(再確認)*"
            :append-inner-icon="visiblePassConf ? 'mdi-eye-off-outline' : 'mdi-eye-outline'"
            :type="visiblePassConf ? 'text' : 'password'"
            v-model="inputs['passConf'].input"
            maxlength="64"
            :error="!!inputs['passConf'].errMsg"
            :error-messages="inputs['passConf'].errMsg"
            @click:append-inner="visiblePassConf = !visiblePassConf"
          >
          </v-text-field>
        </v-row class="pl-2">
      </v-card-text>

      <v-divider></v-divider>

      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn
          class="register-button"
          text="登録内容確認"
          append-icon="mdi-arrow-right-circle-outline"
          @click="validateForm"
        ></v-btn>
      </v-card-actions>
    </v-card>

    <!-- 登録内容確認ダイアログ -->
    <v-dialog v-model="showConfirmDialog" max-width="600">
      <v-card>
        <v-card-title>この内容で登録しますか？</v-card-title>
        <v-card-text>
          <v-table fixed-header density="compact">
            <tbody>
              <tr>
                <td class="text-right">氏名 :</td>
                <td class="text-left">{{ inputs['name'].input }}</td>
              </tr>
              <tr>
                <td class="text-right">氏名(カナ) :</td>
                <td class="text-left">{{ inputs['nameKana'].input }}</td>
              </tr>
              <tr>
                <td class="text-right">メールアドレス :</td>
                <td class="text-left">{{ inputs['mail'].input }}</td>
              </tr>
              <tr>
                <td class="text-right">電話番号 :</td>
                <td class="text-left">{{ inputs['phone'].input }}</td>
              </tr>
              <tr>
                <td class="text-right">パスワード :</td>
                <td class="text-left">＊＊＊＊＊＊＊＊＊＊＊＊</td>
              </tr>
            </tbody>
          </v-table>
        </v-card-text>
        <v-card-actions>
          <v-btn color="primary" @click="showConfirmDialog = false">修正する</v-btn>
          <v-btn
            class="register-button"
            text="登録する"
            append-icon="mdi-account-plus-outline"
            @click="registerUser"
          ></v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- 登録完了ダイアログ -->
    <v-dialog v-model="showCompleteDialog" max-width="300">
      <v-card prepend-icon="mdi-check-circle-outline" title="登録完了">
        <v-card-text>
          <v-table fixed-header density="compact">
            <tbody>
              <tr>
                <td class="text-right">氏名 :</td>
                <td class="text-left">{{ inputs['name'].input }}</td>
              </tr>
              <tr>
                <td class="text-right">ID :</td>
                <td class="text-left">h99999</td>
              </tr>
            </tbody>
          </v-table>
        </v-card-text>
        <v-card-actions>
          <v-btn color="primary" @click="flushAll">閉じる</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
export default {
  name: "RegisterUser",
  data() {
    return {
      visiblePass: false,
      visiblePassConf: false,
      showConfirmDialog: false,
      showCompleteDialog: false,
      inputs: {
        name      : { input: '', errMsg: null},
        nameKana  : { input: '', errMsg: null},
        mail      : { input: '', errMsg: null},
        phone     : { input: '', errMsg: null},
        pass      : { input: '', errMsg: null},
        passConf  : { input: '', errMsg: null},
      },
    }
  },
  computed: {
    phoneNumberRule() {
      return (n) => /^[0-9\-]*$/.test(n) || "数字とハイフンのみ入力可能です";
    },
  },
  methods: {
    /** 電話番号入力チェック */
    validatePhoneNumber(value) {
      // 0-9とハイフン以外の文字を削除
      this.phoneNumber = value.replace(/[^0-9\-]/g, "");
    },
    /** 入力内容確認 */
    validateForm() {
      if (!this.validateInputs()) {
        return;
      }
      this.showConfirmDialog = true;
    },
    /** 各入力項目のValidation処理 */
    validateInputs() {

      for (const key in this.inputs) {
        this.inputs[key].errMsg = null;
      }

      // 「名前」チェック
      if (this.inputs['name'].input === '') {
        this.inputs['name'].errMsg = '必須項目です';
      }
      // 「名前(カナ)」チェック
      if (this.inputs['nameKana'].input === '') {
        this.inputs['nameKana'].errMsg = '必須項目です';
      }
      // 「メール」チェック
      if (this.inputs['mail'].input === '') {
        this.inputs['mail'].errMsg = '必須項目です';
      }
      const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      if (!emailRegex.test(this.inputs['mail'].input)) {
        this.inputs['mail'].errMsg = '正しいメールアドレスを入力してください';
      }
      // 「パスワード」チェック
      if (this.inputs['pass'].input === '') {
        this.inputs['pass'].errMsg = '必須項目です';
      }
      // 「パスワード」チェック
      if (this.inputs['passConf'].input === '') {
        this.inputs['passConf'].errMsg = '必須項目です';
      }

      return Object.values(this.inputs).every(input => !input.errMsg)
    },
    /** ユーザ登録処理 */
    registerUser() {
      // TODO : - Backendに登録リクエストを送信
      //        - ユーザIDをレスポンスで受信
      this.showCompleteDialog = true;
    },
    /** 全ての入力/表示の初期化処理 */
    flushAll() {

      for (const key in this.inputs) {
        this.inputs[key].input = '';
        this.inputs[key].errMsg = null;
      }
      this.showConfirmDialog = false;
      this.showCompleteDialog = false;
    },
  }
};
</script>

<style scoped>
.reg-user {
  align-self: center;
  align-items: center;
  justify-content: center;
}
.register-button {
  align-self: center;
  align-items: center;
  justify-content: center;
  color: white;
  background-color: #339966;
}
</style>
